.container-fluid {
    width: 100%;
    padding: 0;
    margin: 0;
}


.navbar-expand-lg .navbar-collapse{
	justify-content: center;
	font-size: 18px;
	font-family: 'Lato', sans-serif;
	font-weight: 700;

	a.nav-link{
		color: grey;
	}

	a.nav-link:hover{		
		color: grey;
		transform: scale(1.1);		
		text-shadow: 1px 0 5px black;
	}
}

li.nav-item{	
	margin: 0 10px;
	text-align: center;
}



button{			
	border-width: 0px;
	padding: 0;
	align-items: center;
}

section#land{
	height: 100vh;
	overflow: hidden;

	img.img-fluid{
		overflow: hidden;
		width: 100%;
	}

	iframe.spotify{
		position: absolute;
		top: 84vh;
		left: 4vw;
	}
}

section#recital{
	height: 100vh;
	overflow-x: hidden; /* Hide horizontal scrollbar */
    overflow-y: auto; /* Add vertical scrollbar */
    position: relative;

	div {
		position: absolute;
		top: 0;
    	padding: 80px 0 60px;
		

		h1{
			color: #f3230f;
			font-size: 50px;
			font-family: 'Lato';
			font-weight: 900;
			font-style: italic;
			margin-bottom: 30px;
		}

		p{			
			font-size: 18px;
			font-family: 'Lato';
			font-weight: 300;
			line-height: 24px;
		}
	}
}

section#media{
	height: 80vh;
	background-color: #f3230f;

	div.media{
		background-color: black;
		height: 80vh;
		overflow: hidden;
		padding-right: 0px;
		padding-left: 0px;

		.mvideos{
			width: 100%;
		}
		.mvideos:hover{
			position: relative;
			width: 120%;
			left: -10%;
		}
		.mfotos{
			height: 100%;
		}
		.mfotos:hover{
			position: relative;
			transform: scale(1.2);
		}
	}

	.col-md-6{
		padding-right: 0px;
		padding-left: 0px
	}

	.modal-dialog{
		max-width: 75%;
	}

	.close{
		color: #f8f9fa;
	}
}

section#testimonios{

	div.testimonios{
		padding: 60px 0 35px;
		color: #f3230f;

		em{
			font-size: 24px;
			font-family: 'Lato';
			font-weight: 300;			
			font-style: italic;
			line-height: 32px;
		}

		p{
			font-size: 28px;
			font-family: 'Lato';
			font-weight: 900;			
			font-style: italic;
			line-height: 32px;
			margin-top: 20px;
		}
	}

	.form-testimonios{
		padding: 50px 0 35px;
		background: #f3230f;
	}

	.nombre{
		padding-right: 0;
	}

	#testimonio{
		margin: 5px 0;
	}
}

section#integrantes{
	padding: 0 20px;
	background-color: black;

	.col-12{
		padding-right: 7px;
		padding-left: 7px;

	}
	
	div.integrante {
		margin-top: 30px;
		padding-right: 0;
		padding-left: 0;
	}

	h2{
		color: white;
		font-size: 16px;
		font-family: 'Lato';
		font-weight: 700;
		margin-top: 10px;
		margin-bottom: 0;
	}

	p{
		color: white;
		font-size: 16px;
		line-height: 16px;
		font-family: 'Lato';
		font-weight: 300;
		margin-top: 5px;
	}

	img{
		width: 100%;
		border-radius: 50%;
		border-width: 4px;
		border-style: solid;
		border-color: #e6b139;
	}
}

section#videos{
	height: 100vh;
	background: black;

	.container{
		height: 100vh;
		width: 80%;
		padding: 11vh 0 0;
	}

	iframe.sld-video{
		width: 950px;
		height: 534px;
	}

	a.carousel-control-prev{
		height:40vh;
		margin: 30vh 0;
	}

	a.carousel-control-next{
		height:40vh;
		margin: 30vh 0;
	}
}


@media screen and (max-width: 768px){

	iframe.sld-video{
			width: 50%;
		}

	section#recital{
		div {
			padding: 20px 0;
		}
	}

	section#media{
		height: 100vh;

		div.media{
			height: 50vh;
			overflow: hidden;
			padding-right: 0px;
			padding-left: 0px;

		.mfotos{
				width: 100%;
			}
			.mfotos:hover{
				position: relative;
				transform: scale(1.2);
			}
		}
	}

	section#integrantes{
		
		.col-12{
			padding-right: 5px;
			padding-left: 5px;

		}
		
		h4{
			font-size: 10px;
			margin-top: 10px;
			margin-bottom: 0;
		}

		p{
			font-size: 12px;
			line-height: 12px;
			margin-top: 5px;
		}

		img{
			border-width: 2px;
		}
	}

	section#videos{

		iframe.sld-video{
			width: 551px;
			height: 310px;
		}

		.container{
			height: 100vh;
			width: 80%;
			padding: 16vh 0 0;
		}
	}

}

@media screen and (max-width: 400px){
	section#videos{

		iframe.sld-video{
			width: 375px;
			height: 211px;
		}
	}
}